@font-face {
    font-family: YekanBakh;
    src: url(../../assets/fonts/YekanBakh-Regular.ttf);
}
*{
    font-family: YekanBakh;
}

.check-input{
    cursor: pointer;
}

.example-enter {
    opacity: 0.01;
  }
  
  .example-enter.example-enter-active {
    opacity: 1;
    transition: opacity 5000ms ease-in;
  }
  
  .example-leave {
    opacity: 1;
  }
  
  .example-leave.example-leave-active {
    opacity: 0.01;
    transition: opacity 3000ms ease-in;
  }